	module.exports = {"data":{"package":{"id":"bs-date-fns","updated":"2017-12-11T18:34:17.166Z","name":"bs-date-fns","version":"0.1.2","description":"Bucklescript bindings to date-fns","keywords":["date-fns","bucklescript","reason"],"license":"MIT","type":"published","stars":0,"readme":"<h2>Bucklescript bindings to date-fns</h2>\n<p>Bindings for all date-fns functions as of 1.29.0. Currently still missing support for locales.</p>\n<h3>Getting started</h3>\n<pre><code>npm install bs-date-fns --save\n</code></pre>\n<p>Add <code>\"bs-date-fns\"</code> to <code>bs-dependencies</code> in your project's <code>bsconfig.json</code></p>\n<h3>Differences from original date-fns API</h3>\n<p>In bindings the date parameter is moved to last position in a bunch of functions to work better with currying and make better use of pipe operator. Most of these can be seen from types. The ones that have same types but are different from date-fns:</p>\n<pre><code class=\"language-rs\">laterDate |> DateFns.isAfter(earlierDate) /* true */\nDateFns.isAfter(earlierDate, laterDate) /* true */\n\nearlierDate |> DateFns.isBefore(laterDate) /* true */\nDateFns.isBefore(laterDate, earlierDate) /* true */\n\nmiddleDate |> DateFns.isWithinRange(earliestDate, latestDate) /* true */\nDateFns.isWithinRange(earliestDate, latestDate, middleDate) /* true */\n</code></pre>\n","homepageUrl":"https://github.com/SllyQ/bs-date-fns#readme","repositoryUrl":"https://github.com/SllyQ/bs-date-fns","npmUrl":"https://www.npmjs.com/package/bs-date-fns","issuesUrl":"https://github.com/SllyQ/bs-date-fns/issues","slug":"packages/bs-date-fns"}},"pathContext":{"id":"bs-date-fns"}}

